import { useMemo } from 'react'

const uniquePrefix = 'unique-id'
/**    count    */
let count = 0

/**
 * # useUniqueId
 *
 *  ,       .
 *
 * @example
 * ```tsx
 * const uniqueId = useUniqueId()
 * ```
 */
export function useUniqueId(): string {
  const name = useMemo(() => `${uniquePrefix}-${count++}`, [])
  return name
}
