/**
 * # copy
 *
 * clipboard copy  .
 *
 * @param {string} text  
 * @param success    
 * @param failure    
 *
 * @example ```tsx
 * copy('hello world')
 * ```
 */
export function copy(text: string, success?: () => void, failure?: () => void): void {
  navigator.clipboard
    .writeText(text)
    .then(() => {
      success?.()
    })
    .catch(() => {
      failure?.()
    })
}
