import { FC } from 'react'

import { useUniqueId } from '../../hooks/useUniqueId'
import { RadioContext } from './context'
import { RadioGroupProps } from './type'
import { useRadioGroup } from './useRadioGroup'

/**
 * # RadioGroup
 *
 * @description [vercel design radio](https://vercel.com/design/radio)
 *
 *    . {@link Radio}  .
 *
 * @param {RadioGroupProps} {@link RadioGroupProps}  Radio 
 *
 * @example ```tsx
 * <RadioGroup>
 *  <Radio value='value1' />
 *  <Radio value='value2' />
 * </RadioGroup>
 * ```
 */
export const RadioGroup: FC<Partial<RadioGroupProps>> = ({ children, ...props }) => {
  const state = useRadioGroup(props)
  const uniqueId = useUniqueId()

  return (
    <RadioContext
      state={{
        uniqueId,
        ...state
      }}
    >
      {children}
    </RadioContext>
  )
}
